@import '../../App/_var'
@import '../../App/_mixins'

.cake
    &__card 
        min-width: 250px
        width:21vw
        height: fit-content
        border-radius: 15px
        overflow: hidden
        border: $border

    &__card-image 
        width: 100%
        height: 300px
        object-fit: cover
        object-position: center center

    &__card-image:hover 
        cursor: pointer

    &__info
        @include flex-col 
        padding: 2vw
        justify-content: space-between
        gap: 1.5vw

    &__caption 
        width: 80%
        overflow: hidden
        white-space: nowrap
        text-overflow: ellipsis
        font-size: 25px
        font-weight: 600
        font-family: $monts-font
        color: $text-color-brown


    &__weight-container 
        display: flex
        justify-content: space-between

    &__weight 
        font-size: 20px
        font-weight: 500
        font-family: $monts-font
        color: $text-color-brown



    &__weight-title 
        font-size: 20px
        font-weight: 500
        font-family: $monts-font
        color: $text-color-brown


    &__buy-button 
        padding: 15px 0 15px 0
        border-radius: 15px
        background-color: $bg-color-secondary
        color: $text-color-light
        border: none
        text-transform: uppercase
        font-size: 18px
        letter-spacing: 2.5px


    &__buy-button:hover 
        opacity: 0.6
        cursor: pointer
        color: $text-color-hover

@media (max-width: 1200px) 
    .cake
        &__card 
            width:30vw

        &__caption
            font-size: 2.5vw

        &__weight 
            font-size: 2.5vw

        &__weight-title 
            font-size: 2.5vw

        &__buy-button 
            font-size: 1.5vw
            padding: 10px 0 10px 0



@media (max-width: 770px) 
    .cake
        &__card 
            min-width: 0
            width: 80%
        
        &__caption
            font-size: 3vw

        &__buy-button 
            font-size: 2.5vw
            padding: 10px 0 10px 0

        &__card-image
            height: 50vw


@media (max-width: 320px) 
    .cake__card 
        width: 100%
        
        &-image
            height: 200px

